
.video {
    
    position: relative;
    max-height: 50rem;
    overflow: hidden;

    .overlay {
        position: absolute;
        width: 100%;
        height: 100%;
        /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#891c1e+0,9e37f2+69,ffffff+100&0.8+0,0.5+100 */
        background: -moz-linear-gradient(-45deg,  rgba(137,28,30,0.8) 0%, rgba(158,55,242,0.59) 69%, rgba(255,255,255,0.5) 100%); /* FF3.6-15 */
        background: -webkit-linear-gradient(-45deg,  rgba(137,28,30,0.8) 0%,rgba(158,55,242,0.59) 69%,rgba(255,255,255,0.5) 100%); /* Chrome10-25,Safari5.1-6 */
        background: linear-gradient(135deg,  rgba(137,28,30,0.8) 0%,rgba(158,55,242,0.59) 69%,rgba(255,255,255,0.5) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
        filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#cc891c1e', endColorstr='#80ffffff',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */

    }

    video {
        display: block;
        width: 100%;
    }


}

.contenido-video {
    display: flex;
    align-items: center;
    height: 100%;
    padding: 0 10rem;

    h2 {
        display: flex;
        flex-direction: column;
        font-size: $grande;
        color: white;
        align-items: flex-start;

        span {
            background-color: $rosa;
            font-size: $e-pequena;
            padding: 1rem 2rem;
            border-radius:5px;
        }
    }
}