.sobre-festival {
    padding: $separacion;

    @include tablet {
        @include grid(2, 5rem);
    }

    img {
        border-radius: 10px;
    }
}

.contenido-festival {

    display: flex;
    flex-direction: column;
    justify-content: center;

    h2 {
        font-size: $mediana;
    }
    .fecha {
        color: $rosa;
        font-weight: $bold;
    }
    .texto {
        line-height: 1.5;
        text-align: justify;
        font-weight: $delgada;
    }
}

