

.header {
    background-color: $verde;

    .contenido-header {
        padding: $separacion / 2 5rem;

        @include telefono {
            width: 100%;
        }

        @include tablet {
            display: flex;
            justify-content: space-between;
            align-items: center;
        }

        @include tablet {
            display: flex;
            justify-content: space-between;
            align-items: center;
        }
    }

    h1 {
        color: $blanco;
        margin-bottom: 3rem;

        @include telefono {
            text-align: center;
            font-size: $pequena;
        }

        @include tablet {
            margin-bottom: 0;
            font-size: $mediana;
        }

    }

    &.fijo {
        position: fixed;
        width: 100%;
        box-shadow: 0 .3rem .4rem #444 ;
    }
}

.navegacion-principal {
    

    a{
        color: $blanco;
        margin-left: 2rem;

        @include tablet {
            margin-left: 1rem;
        }

        &:hover {
            color: $amarillo;
        }
    }

}


