html {
  font-size: 62.5%;
  box-sizing: border-box;
  font-weight: 900; }

*, *:before, *:after {
  box-sizing: inherit; }

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem; }
  body.fijar-body {
    overflow: hidden; }

p {
  font-size: 2rem;
  color: #000; }

.contenedor {
  max-width: 120rem;
  width: 95%;
  margin: 0 auto; }

a {
  text-decoration: none; }

img {
  max-width: 100%; }

h1 {
  margin: 0; }

h2 {
  margin: 0; }

h3 {
  margin: 0; }

h4 {
  margin: 0; }

.bg-verde {
  background-color: #4CB8B3; }

.bg-amarillo {
  background-color: #FEF46D; }

.header {
  background-color: #4CB8B3; }
  .header .contenido-header {
    padding: 2.5rem 5rem; }
    @media (min-width: 380px) {
      .header .contenido-header {
        width: 100%; } }
    @media (min-width: 768px) {
      .header .contenido-header {
        display: flex;
        justify-content: space-between;
        align-items: center; } }
    @media (min-width: 768px) {
      .header .contenido-header {
        display: flex;
        justify-content: space-between;
        align-items: center; } }
  .header h1 {
    color: #FFF;
    margin-bottom: 3rem; }
    @media (min-width: 380px) {
      .header h1 {
        text-align: center;
        font-size: 2.6rem; } }
    @media (min-width: 768px) {
      .header h1 {
        margin-bottom: 0;
        font-size: 3.5rem; } }
  .header.fijo {
    position: fixed;
    width: 100%;
    box-shadow: 0 .3rem .4rem #444; }

.navegacion-principal a {
  color: #FFF;
  margin-left: 2rem; }
  @media (min-width: 768px) {
    .navegacion-principal a {
      margin-left: 1rem; } }
  .navegacion-principal a:hover {
    color: #FEF46D; }

.video {
  position: relative;
  max-height: 50rem;
  overflow: hidden; }
  .video .overlay {
    position: absolute;
    width: 100%;
    height: 100%;
    /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#891c1e+0,9e37f2+69,ffffff+100&0.8+0,0.5+100 */
    background: -moz-linear-gradient(-45deg, rgba(137, 28, 30, 0.8) 0%, rgba(158, 55, 242, 0.59) 69%, rgba(255, 255, 255, 0.5) 100%);
    /* FF3.6-15 */
    background: -webkit-linear-gradient(-45deg, rgba(137, 28, 30, 0.8) 0%, rgba(158, 55, 242, 0.59) 69%, rgba(255, 255, 255, 0.5) 100%);
    /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(135deg, rgba(137, 28, 30, 0.8) 0%, rgba(158, 55, 242, 0.59) 69%, rgba(255, 255, 255, 0.5) 100%);
    /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#cc891c1e', endColorstr='#80ffffff',GradientType=1 );
    /* IE6-9 fallback on horizontal gradient */ }
  .video video {
    display: block;
    width: 100%; }

.contenido-video {
  display: flex;
  align-items: center;
  height: 100%;
  padding: 0 10rem; }
  .contenido-video h2 {
    display: flex;
    flex-direction: column;
    font-size: 4rem;
    color: white;
    align-items: flex-start; }
    .contenido-video h2 span {
      background-color: #F53756;
      font-size: 2rem;
      padding: 1rem 2rem;
      border-radius: 5px; }

.sobre-festival {
  padding: 5rem; }
  @media (min-width: 768px) {
    .sobre-festival {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      column-gap: 5rem; } }
  .sobre-festival img {
    border-radius: 10px; }

.contenido-festival {
  display: flex;
  flex-direction: column;
  justify-content: center; }
  .contenido-festival h2 {
    font-size: 3.5rem; }
  .contenido-festival .fecha {
    color: #F53756;
    font-weight: 700; }
  .contenido-festival .texto {
    line-height: 1.5;
    text-align: justify;
    font-weight: 300; }

.lineup {
  background-color: #F53756;
  padding: 5rem 0; }
  .lineup h3, .lineup p {
    text-align: center;
    color: #FFF; }
  .lineup p.dia {
    text-transform: uppercase;
    margin: 5rem 0; }

@media (min-width: 768px) {
  .escenarios-contenedor {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 1rem; } }

.escenarios-contenedor .escenario {
  padding-top: 2.5rem; }

.escenarios-contenedor .nombre-escenario {
  color: #000;
  font-size: 2rem;
  font-weight: 300; }
  .escenarios-contenedor .nombre-escenario:after {
    content: '';
    width: 20rem;
    height: 10rem;
    display: block;
    margin: 2rem auto 0 auto;
    background-size: 15rem;
    background-repeat: no-repeat;
    background-position: center center; }

.edm .nombre-escenario:after {
  background-image: url("../img/icono_edm.png"); }

.rock .nombre-escenario:after {
  background-image: url("../img/icono_rock.png"); }

.calendario {
  padding: 0;
  list-style: none; }
  .calendario li {
    padding: 2.5rem;
    font-weight: 700; }
    .calendario li span {
      font-weight: 400; }

.bg-verde .calendario li:nth-child(2n + 1) {
  background-color: #3b9691; }

.bg-amarillo .calendario li:nth-child(2n+1) {
  background-color: #fef03a; }

.galeria {
  padding: 5rem 0; }
  .galeria h3 {
    text-align: center; }

.galeria-imagenes {
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 1rem; }
  .galeria-imagenes li img {
    border-radius: 15px;
    cursor: pointer;
    transition: all .3s ease; }
    .galeria-imagenes li img:hover {
      transform: scale(1.5);
      box-shadow: 0px 0px 50px #000; }
  @media (min-width: 768px) {
    .galeria-imagenes {
      display: grid;
      grid-template-columns: repeat(4, 1fr);
      column-gap: 1rem; } }
  @media (min-width: 1200px) {
    .galeria-imagenes {
      display: grid;
      grid-template-columns: repeat(6, 1fr);
      column-gap: 1rem; } }

.overlay {
  position: fixed;
  background-color: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100%;
  top: 0;
  display: grid;
  place-items: center; }
  .overlay img {
    width: 50%;
    height: 80%; }

.btnCerrar {
  width: 5rem;
  height: 5rem;
  background-color: #FFF;
  display: grid;
  place-items: center;
  font-size: 3rem;
  border-radius: 50%; }
  .btnCerrar:hover {
    cursor: pointer; }
