
.galeria {
    padding: $separacion 0;

    h3{
        text-align: center;
    }
}

.galeria-imagenes {
    padding: 0;
    list-style: none;
    
    li {
        img {
            border-radius: 15px;
            cursor: pointer;
            transition: all .3s ease;

            &:hover {
                transform: scale(1.5);
                box-shadow: 0px 0px 50px #000;     
            }
        }
    }

    @include grid(2, 1rem);

    @include tablet {

        @include grid(4, 1rem); 
    }

    @include desktop {

        @include grid(6, 1rem); 
    }


}

.overlay {
    position: fixed;
    background-color: rgba(0, 0, 0, .5);
    width: 100%;
    height: 100%;
    top: 0;
    display: grid;
    place-items: center;

    img {
        width: 50%;
        height: 80%;
    }
}

.btnCerrar {
    width: 5rem;
    height: 5rem;
    background-color: $blanco;
    display: grid;
    place-items: center;
    font-size: 3rem;
    border-radius: 50%;

    &:hover {
        cursor: pointer;
    }
}
